@import './colors.scss';
@import './responsive.scss';


*,
*:before,
*:after {
  box-sizing: inherit;
  user-select: none;
  outline: none;
  -webkit-touch-callout: none;
}

* {
  position: relative;
  margin: 0;
  padding: 0;
}

// Modification du design de la ScrollBar
// ***********************************************************

//* CACHER COMPLETEMENT LA SCROLL-BAR - commenter le reste du code et décommenter ces 3 lignes
// *::-webkit-scrollbar {
//       width: 0px;
// // display: none;
// }

/* Works on Firefox */
* {
  scrollbar-width: thin;
  // scrollbar-color: rgb(26, 25, 25) rgb(255, 255, 255);
}

/* Works on Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 7px;
}

*::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0);
}

*::-webkit-scrollbar-thumb {
  border-radius: 20px;
  background-color: rgb(100, 99, 99);
  border: 3px solid rgba(255, 255, 255, 0);
}
// *************************************************************




html {
    // background: $color-white;
    // color: $color-white;
    box-sizing: border-box;
    font-size: calc(100vw / 1920 * 10);
    height: 100%;
    left: 0;
    overflow-x: hidden;
    // overflow: hidden;
    // overscroll-behavior: none;
    // position: fixed;
    // -moz-osx-font-smoothing: grayscale;
    // top: 0;
    // -webkit-font-smoothing: antialiased;
    width: 100%;
  
    @include media('<=phone') {
      font-size: calc(100vw / 375 * 10);
    }
}

a {
    color: inherit;
    outline: none;
    pointer-events: auto;
    text-decoration: none;
}